@layer layout {
  .container,
  .container-fluid,
  .container-2xl,
  .container-xl,
  .container-lg,
  .container-md,
  .container-sm {
    --gutter-x: 1.5rem;
    --gutter-y: 0;
    width: 100%;
    padding-inline: calc(var(--gutter-x) * 0.5);
    margin-inline: auto;
  }
  @media (width >= 576px) {
    .container-sm, .container {
      max-width: 540px;
    }
  }
  @media (width >= 768px) {
    .container-md, .container-sm, .container {
      max-width: 720px;
    }
  }
  @media (width >= 1024px) {
    .container-lg, .container-md, .container-sm, .container {
      max-width: 960px;
    }
  }
  @media (width >= 1280px) {
    .container-xl, .container-lg, .container-md, .container-sm, .container {
      max-width: 1200px;
    }
  }
  @media (width >= 1536px) {
    .container-2xl, .container-xl, .container-lg, .container-md, .container-sm, .container {
      max-width: 1440px;
    }
